<template>
	<view>
		<view class="loginBox">
			
		<h1>手机号登录</h1>
			
		<input v-model="phone" @blur="zhenzhe" type="text" placeholder="填写手机号" />
	
		<view class="han">
		<input v-model="imgYzm" type="text" placeholder="请填写图形验证码" />
		<view v-html="yzmData" style="background-color:black;"></view>
		</view>
		
		<view class="han">
		<input v-model="phoneYzm" type="text" placeholder="输入手机验证码"/>
		<button @click="obtainYzm" v-text="yzmBtn" :disabled="yzmBtnDis"></button>
		</view>
		
		<button @click="login" class="loginBtn">登录</button>
		
		</view>
	
	</view>
</template>

<script>
	import {captcha,checkImgYzm,h5login} from "api/login.js"
	
	export default {
		data() {
			return {
				captchaId: '',
				yzmData: '',
				
				phone: '',
				imgYzm: '',
				phoneYzm: '',
				yzmBtn: '获取验证码',
				yzmBtnDis: false,
			}
		},
		methods: {
			// 手机号正则
			zhenzhe(){
				// let number = /^1(3\d|4[5-9]|5[0-35-9]|6[2567]|7[0-8]|8\d|9[0-35-9])\d{8}$/
				// if(!this.phone.match(number)){
				// 	console.log("错")
				// }
				this.$toast("用户名或密码有误")
			},
			// 点击获取验证码
			async obtainYzm(){
				let {data} = await checkImgYzm(this.phone,this.captchaId,this.imgYzm)

				let num = 60;
				let setInt = setInterval(()=>{
					num--
					this.yzmBtn = `重新发送(${num})`
					this.yzmBtnDis = true
					if(num == 0){
						clearInterval(setInt)
						this.yzmBtnDis = false
						this.yzmBtn = "发送验证码"
					}
					
				},1000)
				
			},
			async login(){
				// if(){
					
				// }
				let {data} = await h5login()
			}
		},
		async created(){
			let {data} = await captcha()
			this.captchaId = data.captchaId
			this.yzmData = data.data
			
		},
	}
</script>

<style lang="scss">
	
.loginBox{
	width: 500px;
	border: 2px solid black;
	margin: auto;
	margin-top: 100px;
	padding: 30px;
	
	h1{
		text-align: center;
	}
	
	>input{
		border-bottom: 1px solid #ccc;
	}
	
	input{
		width: 100%;
		padding:10px;
		margin: 6px 0;
	}
	
	.han{
		display: flex;
		justify-content: space-between;	
		align-items: center;
		border-bottom: 1px solid #ccc;
		button{
			width: 140px;
			font-size: 14px;
			margin: 0;
		}
	}
	
	.loginBtn{
		background-color: #56bcff;
		width: 50%;
		color: white;
		margin: 30px auto;
	}
}

</style>
